'use client';

import React, { useEffect, useState } from "react";
import { useParams } from 'next/navigation';
import axios from 'axios';
import '../../../../public/styles/game_start.css';
import { getAccessToken, getValidAccessToken, getMembershipId } from '../../../../../token';

const API_BASE_URL = process.env.NEXT_PUBLIC_API_BASE_URL || "https://ec2-3-34-134-27.ap-northeast-2.compute.amazonaws.com/api/wargame-problems";
const FILE_BASE_URL = API_BASE_URL.replace('/api/wargame-problems', '') || "https://ec2-3-34-134-27.ap-northeast-2.compute.amazonaws.com";

interface Problem {
  title: string;
  creator: string;
  level: number;
  problemFile?: string;
  content: string;
  dockerfileLink?: string;
}

interface Ranking {
  nickname: string;
  created: string;
}

interface ApiResponse<T> {
  result: T;
}

const CTFProblemPage = () => {
  const params = useParams();
  const problemId = params?.id || "6";

  const [problem, setProblem] = useState<Problem | null>(null);
  const [flag, setFlag] = useState<string>("");
  const [message, setMessage] = useState<string>("");
  const [comments, setComments] = useState<string[]>([]);
  const [newComment, setNewComment] = useState<string>("");
  const [ranking, setRanking] = useState<Ranking[]>([]);
  const [vmAddress, setVmAddress] = useState<string>("");
  const [token, setToken] = useState<string | null>(null);
  const [isTokenLoaded, setIsTokenLoaded] = useState(false);

  useEffect(() => {
    const storedToken = getAccessToken();
    setToken(storedToken);
    setIsTokenLoaded(true); // ÌÜ†ÌÅ∞ Î°úÎî© ÏôÑÎ£å
  }, []);

  useEffect(() => {
    if (!isTokenLoaded) return; // ÌÜ†ÌÅ∞ Î°úÎî© ÏôÑÎ£å Ï†ÑÏóêÎäî Ïã§ÌñâÌïòÏßÄ ÏïäÏùå

    if (!token) {
      alert("ÌÜ†ÌÅ∞Ïù¥ Ï°¥Ïû¨ÌïòÏßÄ ÏïäÏäµÎãàÎã§. Î°úÍ∑∏Ïù∏ ÌõÑ Îã§Ïãú ÏãúÎèÑÌï¥Ï£ºÏÑ∏Ïöî.");
      return;
    }

    // Î¨∏Ï†ú Îç∞Ïù¥ÌÑ∞ Í∞ÄÏ†∏Ïò§Í∏∞
    const fetchProblem = async () => {
      try {
        const res = await fetch(`${API_BASE_URL}/${problemId}`, {
          method: 'GET',
          headers: {
            'Authorization': `Bearer ${token}`,
            'Accept': '*/*',
          },
        });

        if (!res.ok) {
          throw new Error(`Î¨∏Ï†ú API Ìò∏Ï∂ú Ïã§Ìå®: ${res.status} ${res.statusText}`);
        }

        const data: ApiResponse<Problem> = await res.json();
        setProblem(data.result);
      } catch (error) {
        console.error("Î¨∏Ï†ú Í∞ÄÏ†∏Ïò§Í∏∞ Ïã§Ìå®:", error);
      }
    };

    // Îû≠ÌÇπ Îç∞Ïù¥ÌÑ∞ Í∞ÄÏ†∏Ïò§Í∏∞
    const fetchRanking = async () => {
      try {
        const res = await fetch(`${FILE_BASE_URL}/api/problems/${problemId}/firstblood?size=3`, {
          method: 'GET',
          headers: {
            'Authorization': `Bearer ${token}`,
            'Accept': '*/*',
          },
        });

        if (!res.ok) {
          throw new Error(`Îû≠ÌÇπ API Ìò∏Ï∂ú Ïã§Ìå®: ${res.status} ${res.statusText}`);
        }

        const data: ApiResponse<Ranking[]> = await res.json();
        setRanking(data.result);
      } catch (error) {
        console.error("Îû≠ÌÇπ Ï°∞Ìöå Ïã§Ìå®:", error);
      }
    };

    fetchProblem();
    fetchRanking();
  }, [problemId, token, isTokenLoaded]);

  const handleSubmit = async () => {
    if (!token) return;

    try {
      const url = `${FILE_BASE_URL}/api/problems/${problemId}/solve?flag=${encodeURIComponent(flag)}`;

      const res = await fetch(url, {
        method: "GET",
        headers: {
          "Authorization": `Bearer ${token}`,
          "Accept": "*/*",
        },
      });

      if (!res.ok) {
        setMessage("Ï†ïÎãµ ÌôïÏù∏ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
        return;
      }

      const data = await res.json();
      setMessage(data.result ? "Ï†ïÎãµÏûÖÎãàÎã§! Ï∂ïÌïòÌï©ÎãàÎã§." : "Ïò§ÎãµÏûÖÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌïòÏÑ∏Ïöî.");
    } catch (error) {
      console.error("Ï†ïÎãµ ÌôïÏù∏ Ïã§Ìå®:", error);
      setMessage("Ï†ïÎãµ ÌôïÏù∏ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§.");
    }
  };

  const handleAddComment = () => {
    if (newComment.trim() !== "") {
      setComments([...comments, newComment]);
      setNewComment("");
    }
  };

  const handleShowVmAddress = () => {
    setVmAddress(problem?.dockerfileLink || "VM Ï£ºÏÜåÍ∞Ä Ï†úÍ≥µÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.");
  };

  const handleDeleteProblem = async () => {
    if (!token) {
      alert("ÌÜ†ÌÅ∞Ïù¥ Ï°¥Ïû¨ÌïòÏßÄ ÏïäÏäµÎãàÎã§.");
      return;
    }

    try {
      const accessToken = await getValidAccessToken();
      const membershipId = getMembershipId();

      if (!accessToken || !membershipId) {
        alert("Î°úÍ∑∏Ïù∏ ÏÉÅÌÉúÍ∞Ä ÏïÑÎãôÎãàÎã§.");
        return;
      }

      const response = await axios.delete(`${API_BASE_URL}/${problemId}`, {
        headers: {
          Authorization: `Bearer ${accessToken}`,
          'Content-Type': 'application/json',
        },
      });

      if (response.status === 200) {
        alert("Í≤åÏãúÍ∏ÄÏù¥ ÏÑ±Í≥µÏ†ÅÏúºÎ°ú ÏÇ≠Ï†úÎêòÏóàÏäµÎãàÎã§.");
        // ÏÇ≠Ï†ú ÌõÑ, ÌéòÏù¥ÏßÄ Î¶¨ÎîîÎ†âÏÖò ÎòêÎäî ÏÉÅÌÉú Í∞±Ïã† Îì±Ïùò Ï≤òÎ¶¨
      }
    } catch (error) {
      console.error("Í≤åÏãúÍ∏Ä ÏÇ≠Ï†ú Ïã§Ìå®:", error);
      alert("Í≤åÏãúÍ∏Ä ÏÇ≠Ï†úÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§.");
    }
  };

  if (!problem) return <div>Î°úÎî© Ï§ë...</div>;

  const formatTime = (timeString: string) => {
    const date = new Date(timeString);
    const year = date.getFullYear();
    const month = date.getMonth() + 1;
    const day = date.getDate();
    const hours = date.getHours();
    const minutes = date.getMinutes();
    const seconds = date.getSeconds();

    return `${year}-${month < 10 ? '0' : ''}${month}-${day < 10 ? '0' : ''}${day} ${hours}:${minutes < 10 ? '0' : ''}${minutes}:${seconds < 10 ? '0' : ''}${seconds}`;
  };

  return (
    <div className="container">
      <div className="card">
        <h1>
          {problem.title}{" "}
          <span style={{fontSize: '0.5em'}}>made by {problem.creator}</span>
        </h1>

        <p>
          Level: <span style={{color: "gold"}}>{'‚≠ê'.repeat(problem.level)}</span>
        </p>

        {problem.problemFile ? (
          <p>
            Î¨∏Ï†ú ÌååÏùº:{" "}
            <a
              href={`${FILE_BASE_URL}/${problemId}/download`}
              download
              target="_blank"
              rel="noopener noreferrer"
              style={{color: "blue", textDecoration: "underline"}}
            >
              Îã§Ïö¥Î°úÎìú
            </a>
          </p>
        ) : (
          <p>Î¨∏Ï†ú ÌååÏùº ÏóÜÏùå</p>
        )}
        <p>{problem.content}</p>

        <input
          type="text"
          placeholder="ÌîåÎûòÍ∑∏Î•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî"
          value={flag}
          onChange={(e) => setFlag(e.target.value)}
          className="input"
        />
        <button onClick={handleSubmit} className="button">Ï†úÏ∂ú</button>
        {message && <p className="message">{message}</p>}

        {/* Í≤åÏãúÍ∏Ä ÏÇ≠Ï†ú Î≤ÑÌäº */}
        <button onClick={handleDeleteProblem} className="delete-button">
          Í≤åÏãúÍ∏Ä ÏÇ≠Ï†ú
        </button>
      </div>

      <div className="ranking-box">
        <h3>Îû≠ÌÇπ</h3>
        {ranking.length > 0 ? (
          ranking.map((user, index) => (
            <div key={index} className="ranking-item">
              <span className="rank">
                {index === 0 ? "üëë" : index + 1}
              </span>
              <span className="username">{user.nickname}</span>
              <span className="solved-time">{formatTime(user.created)}</span>
            </div>
          ))
        ) : (
          <p>ÏïÑÏßÅ ÌíÄÏù¥Ìïú ÏÇ¨Ïö©ÏûêÍ∞Ä ÏóÜÏäµÎãàÎã§.</p>
        )}
      </div>

      <div className="card">
        <h2>VM Ï£ºÏÜå</h2>
        <button onClick={handleShowVmAddress} className="button vm-button">
          VM Ï£ºÏÜå Î≥¥Í∏∞
        </button>
        {vmAddress && <p className="vm-address">{vmAddress}</p>}
      </div>

      <div className="card">
        <h2>ÎåìÍ∏Ä</h2>
        <textarea
          className="textarea"
          placeholder="ÎåìÍ∏ÄÏùÑ ÏûëÏÑ±ÌïòÏÑ∏Ïöî"
          value={newComment}
          onChange={(e) => setNewComment(e.target.value)}
        ></textarea>
        <button onClick={handleAddComment} className="submit-button comment-button">
          ÎåìÍ∏Ä ÏûëÏÑ±
        </button>
        <div className="comment-list">
          {comments.map((comment, index) => (
            <div key={index} className="comment-item">
              <p className="comment-text">{comment}</p>
              <div className="reply-section">
                <button className="reply-button">ÎåÄÎåìÍ∏Ä</button>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

export default CTFProblemPage;
